.filter{
    position: sticky;
    top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    background: #fff;
    margin: 30px;
    padding: 30px;
    box-shadow: 4px 4px 4px gray;
    border-radius: 10px;
}
.filter input,button{
    font-size: 18px;
    font-family: 'Poppins', sans-serif;
}
.filter button{
    padding: 6px 10px;
    background: black;
    color: white;
    border: none;
    border-radius: 10px;
    
}