@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
.repo-card{
    background: #fff;
    font-family: 'Poppins', sans-serif;
    padding: 3%;
    margin: 2%;
    /* border: 1px solid black; */
    border-radius: 10px;
    /* outline: 1px solid black; */
    box-shadow: 4px 4px 2px grey;
    transition: 1s;
}
.avatar{
    width: 100px;
    border-radius: 100%;
}
.card-top{
    display: flex;
    align-items: center;
    gap: 2em;
}
.views{
    display: flex;
    align-items: center;
    gap: 8px;
}
.social_details{
    display: flex;
    width: 100%;
    justify-content: flex-start;
    gap: 12px;
}
.card-desc{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 6px;
}
.repo_name{
    font-size: 1.5em;
    font-weight: 800;
}
.repo-card-bottom{
    display: none;
}
.repo-card-bottom{
    transition: 1s;
}
.show{
    display: initial;
}
.topics{
    display: grid;
    grid-template-columns: repeat(5,1fr);
    gap: 10px;
}
.topic{
    padding: 5px 8px;
    background: grey;
    margin: 5px;
    text-align: center;
}
.license{
    display: flex;
    flex-direction: column;
}
.links{
    display: flex;
    flex-direction: column;
}
.add_data{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
.license{
    display: flex;
}
.license_head{
    font-weight: 600;
}